
.cartcontrol {
  font-size: 0;
  height: 100%;
}
.cartcontrol div {
    display: inline-block;
    font-size: 48px;
    color: #00a0dc;
}
.cartcontrol .count {
    font-size: 20px;
    color: #2f999f;
    line-height: 48px;
    display: inline-block;
    vertical-align: bottom;
    width: 48px;
    text-align: center;
}
.cartcontrol .fade-enter-active, .cartcontrol .fade-leave-active {
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}
.cartcontrol .fade-enter, .cartcontrol .fade-leave-to {
    opacity: 0;
}
.cartcontrol .move-enter {
    -webkit-transform: translate3d(48px, 0, 0);
            transform: translate3d(48px, 0, 0);
}
.cartcontrol .move-enter-to {
    -webkit-transform: rotate3D(0, 0, 1, 180deg) translate3d(0, 0, 0);
            transform: rotate3D(0, 0, 1, 180deg) translate3d(0, 0, 0);
}
.cartcontrol .move-leave {
    -webkit-transform: rotate3D(0, 0, 1, 0deg);
            transform: rotate3D(0, 0, 1, 0deg);
}
.cartcontrol .move-leave-to {
    -webkit-transform: rotate3D(0, 0, 1, -180deg) translateX(-48px);
            transform: rotate3D(0, 0, 1, -180deg) translateX(-48px);
}
.cartcontrol .move-enter-active, .cartcontrol .move-leave-active {
    -webkit-transition: all .5s linear;
    transition: all .5s linear;
}
.cartcontrol .ball {
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background: #00a0dc;
    position: absolute;
    top: 0;
    right: 12px;
    z-index: 100;
}



