
h3 {
  font-size: 32px;
  line-height: 32px;
}
h4 {
  font-size: 28px;
  line-height: 28px;
}
h5 {
  font-size: 24px;
  line-height: 24px;
}
.header {
  font-size: 0;
  position: relative;
  background: rgba(7, 17, 27, 0.5);
}
.header .logo, .header .information {
    display: inline-block;
    vertical-align: top;
}
.header .logo {
    margin: 0.64rem 0.42667rem 0.48rem 0.64rem;
    width: 1.70667rem;
    height: 1.70667rem;
}
.header .logo img {
      width: 100%;
      height: 100%;
}
.header .information {
    margin-top: 0.64rem;
    color: white;
    line-height: 24px;
    font-weight: 200;
}
.header .information .name {
      margin-top: 0.05333rem;
      margin-bottom: 0.21333rem;
      font-size: 32px;
      font-weight: bold;
      line-height: 36px;
}
.header .information .name .brand {
        margin-right: 0.16rem;
        width: 60px;
        height: 36px;
}
.header .information .description {
      font-size: 24px;
      margin-bottom: 0.26667rem;
}
.header .information .support {
      font-size: 20px;
}
.header .information .support .icon {
        width: 24px;
        height: 24px;
}
.header .information .support-num {
      position: absolute;
      right: 0.32rem;
      bottom: 0.48rem;
      height: 0.64rem;
      margin-bottom: -0.13333rem;
      background: rgba(0, 0, 0, 0.2);
      font-size: 20px;
      padding-left: 0.21333rem;
      padding-right: 0.21333rem;
      border-radius: 0.32rem;
}
.header .information .support-num p {
        display: inline;
        line-height: 48px;
        font-weight: 200;
        color: white;
}
.header .information .support-num p i {
          vertical-align: middle;
}
.header .detail {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    background: rgba(7, 17, 27, 0.8);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100vw;
    min-height: 100vh;
    overflow: auto;
}
.header .detail section {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
}
.header .detail section div {
        margin: 0 auto;
}
.header .detail section .name {
        margin-top: 1.70667rem;
}
.header .detail section .star {
        width: 4.98667rem;
        margin-top: 0.42667rem;
}
.header .detail section .partition {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-top: 0.74667rem;
        margin-bottom: 0.64rem;
        width: 8.08rem;
}
.header .detail section .partition h4 {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
}
.header .detail section .partition hr {
          margin: 0;
          width: 2.98667rem;
          height: 3px;
          border: none;
          background-color: rgba(255, 255, 255, 0.2);
          -ms-flex-item-align: center;
              align-self: center;
}
.header .detail section .partition h4 {
          padding: -0.32rem 0;
          width: 1.46667rem;
}
.header .detail section .supportDetail, .header .detail section .bulletin {
        width: 7.44rem;
        margin: 0 auto;
        text-align: left;
        font-size: 24px;
        font-weight: 200;
        color: white;
}
.header .detail section .supportDetail li, .header .detail section .bulletin li {
          list-style: none;
          line-height: 24px;
          margin-bottom: 24px;
}
.header .detail section .supportDetail li span, .header .detail section .bulletin li span {
            width: 32px;
            height: 32px;
}
.header .detail section .bulletin {
        line-height: 48px;
}
.header .detail footer {
      width: 100vw;
      height: 1.70667rem;
      text-align: center;
      font-size: 0.86667rem;
      color: rgba(255, 255, 255, 0.5);
      margin-bottom: 1rem;
}
.header .detail h3, .header .detail h4 {
      color: white;
      font-weight: 700;
}



