
.food {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  background: #f3f5f7;
}
.food div[class|="info"] {
    background: #fff;
}
.food .back {
    position: absolute;
    left: 0.5rem;
    top: 0.5rem;
    padding: 0.1rem;
}
.food .back .icon-arrow_lift {
      color: #fff;
      font-size: 0.4rem;
}
.food .food-content {
    padding-bottom: 2rem;
}
.food .text, .food .info-intro {
    padding: 0 0.48rem;
}
.food .info-basic {
    padding-bottom: 36px;
    border-bottom: 2px solid rgba(7, 17, 27, 0.1);
}
.food .info-basic img {
      display: block;
      width: 100vw;
}
.food .info-basic p {
      font-size: 0.26667rem;
}
.food .info-basic .text {
      position: relative;
}
.food .info-basic .name {
      font-size: 0.37333rem;
      font-weight: 700;
      color: #07111b;
      line-height: 28px;
      margin-top: 36px;
      margin-bottom: 16px;
}
.food .info-basic .sales, .food .info-basic .rating {
      color: #93999f;
      line-height: 20px;
      display: inline;
}
.food .info-basic .sales {
      margin-right: 24px;
}
.food .info-basic .price {
      line-height: 48px;
      font-weight: 700;
      color: #f01414;
      font-size: 0.37333rem;
      margin-top: 36px;
}
.food .info-basic .price .oldPrice {
        font-size: 0.26667rem;
        font-weight: 700;
        color: #93999f;
        margin-left: 10px;
        text-decoration: line-through;
}
.food .info-basic .ShopCart-wrapper {
      position: absolute;
      right: 0.48rem;
      bottom: 0;
}
.food .info-basic .ShopCart-wrapper span {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        display: inline-block;
        height: 0.64rem;
        width: 1.97333rem;
        color: white;
        background-color: #00a0dc;
        font-size: 0.26667rem;
        line-height: 0.32rem;
        padding: 0.16rem 0.32rem;
        border-radius: 0.32rem;
}
.food .info-intro {
    margin-top: 32px;
    margin-bottom: 32px;
    padding-top: 36px;
    border: 1px solid rgba(7, 17, 27, 0.1);
    border-left: none;
    border-right: none;
}
.food .info-intro .intro-title {
      font-size: 0.37333rem;
      font-weight: 700;
      color: #07111b;
      line-height: 28px;
      margin-bottom: 12px;
}
.food .info-intro .intro-detail {
      font-size: 0.32rem;
      font-weight: 200;
      line-height: 0.64rem;
      color: #4d555d;
      padding: 16px;
      padding-bottom: 36px;
}



